.Main {
  padding: 0 1.25rem;
}

.EventList {
  padding: 0;
  list-style: none;
}

.Event {
  margin-bottom: 10px;

  border-radius: 2px;
  background-color: #161616;

  &:last-child {
    margin-bottom: 0;
  }

  a {
    display: block;
    padding: 12px 14px;

    font-size: 15px;
    font-weight: 700;
    color: #fff;
    text-decoration: none;
  }
}
